import {List, SimpleListItem} from '@rmwc/list'
import '@rmwc/list/styles';

function ListContainer( {schedule, info, periods, day} ){
    if (day === 0 || day === 6) return null;
    return (
        <List twoLine >
            {
                
                schedule[day].map((a, b) => {
                    return (
                        <SimpleListItem
                            graphic={info[a].icon}
                            text={a}
                            secondaryText={periods[b]}
                        />
                    )
                })
            }

        </List>
    )
}

export default ListContainer