import {CollapsibleList, SimpleListItem} from '@rmwc/list'
import {TextField} from '@rmwc/textfield'
import mdDateTimePicker from 'md-date-time-picker'
import '@rmwc/list/styles';
import '@rmwc/textfield/styles';
import {Fab} from '@rmwc/fab'
import {useState, createRef} from 'react'
import {Dialog, DialogTitle, DialogContent, DialogButton, DialogActions} from '@rmwc/dialog'
import '@rmwc/dialog/styles';
import '@rmwc/fab/styles';
import Nextline from './Nextline'
import Pagination from './Pagination'

function Todos({toggle}){
    var input = createRef()
        var dialog = new mdDateTimePicker({"type": "date"})
        var time = dialog.time
        const [open, setOpen] = useState(false)
    
    
    return(
        <>
      <div class="list">
    <CollapsibleList
    handle={
      <SimpleListItem
        text="Today"
        metaIcon="chevron_right"
      />
    }
  >
    <SimpleListItem text="Sleep" />
    <SimpleListItem text="Shit" />
    <SimpleListItem text="Lack of motivtation" />
  </CollapsibleList>
  <CollapsibleList
    handle={
      <SimpleListItem
        text="Tomorrow"
        metaIcon="chevron_right"
      />
    }
  >
    <SimpleListItem text="Eat ass" />
    <SimpleListItem text="Live fast" />
    <SimpleListItem text="Sleep deprivation" />
  </CollapsibleList>
  <CollapsibleList
    handle={
      <SimpleListItem
        text="9/7"
        metaIcon="chevron_right"
      />
    }
  >
    <SimpleListItem text="Stomach hurts" />
    <SimpleListItem text="Feel worse" />
    <SimpleListItem text="Try not to cry out loud" />
  </CollapsibleList>
  </div>
  <Dialog
        open={open}
        onClose={evt => {
          console.log(evt.detail.action);
          setOpen(false);
        }}
        onClosed={evt => console.log(evt.detail.action)}
      >
        <DialogTitle>Add Todo</DialogTitle>
        <DialogContent>
        <TextField label="Name" />
        <Nextline />
        <TextField trailingIcon={{
      icon: 'event',
      onClick: () => {
        input.current.querySelector('input').value = ' '
                document.querySelector('.mddtp-picker__subtitle').classList.remove('mddtp-picker__color--active');
                document.querySelector('.mddtp-picker__title').classList.add('mddtp-picker__color--active')
                

          
          dialog.toggle(); 
          console.log(dialog.time.toString()); 
          document.querySelector('#mddtp-date__ok').addEventListener('click', () => {
            
              input.current.querySelector('input').value = dialog.time.format("ddd[,] MMM D")
            })
        }
    }} ref={input} label="Date" />
    
    
        </DialogContent>
        <DialogActions>
          <DialogButton action="close">Cancel</DialogButton>
          <DialogButton action="accept" isDefaultAction>
            Add
          </DialogButton>
        </DialogActions>
      </Dialog>
      <Fab raised icon="add" onClick={() => setOpen(true)} />
      
    </>
  )
}
export default Todos